.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  margin: 20px 0;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  height: 25vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #003366; /* Lighter Navy Blue */
  color: #ffffff; /* White text color */
  font-size: calc(12px + 3vmin); /* Increase font size */
  padding: 20px;
  box-sizing: border-box;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Game-start {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #003366; /* Lighter Navy Blue */
  color: #ffffff; /* White text color */
  font-size: calc(12px + 3vmin); /* Increase font size */
}

.Game-start div {
  margin: 10px 0;
}

body {
  font-family: 'Caveat', cursive;
}

.App-header, .Game-start {
  font-family: 'Caveat', cursive;
}

.scrollable-text {
  max-height: 200px; /* Set a maximum height for the box */
  overflow-y: auto; /* Enable vertical scrolling */
  padding: 10px;
  border: 1px solid #004080; /* Darker border color */
  background-color: #e6f2ff; /* Light blue background */
  color: #003366; /* Dark blue text color */
  font-size: 0.8em; /* Slightly smaller font size */
  margin: 10px 0; /* Space around the box */
}
